<template>
  <div class="Menu">
    <mu-drawer :open="open" @close="closeMenu">
      <mu-appbar :title="title" @click.native="closeMenu"/>
      <mu-list>
        <mu-list-item title="主页" @click.native="goHome">
          <mu-icon slot="left" value="inbox"/>
        </mu-list-item>
          <mu-list-item title="教师管理" @click.native="goTeacherManage">
        <mu-icon slot="left" value="inbox"/>
        </mu-list-item>
      </mu-list>
    </mu-drawer>
  </div>
</template>
<script>
  export default {
    name: 'Menu',
    props: ['open', 'title'],
    methods: {
      openMenu () {
        this.open = true
      },
      closeMenu () {
        this.open = false
        this.$emit('closeMenu')
      },
      goHome () {
        window.location.href = '/'
      },
      goTeacherManage () {
        window.location.href = '/teacher'
      }
    }
  }
</script>



